*{
    font-family: 'Playfair Display', serif;
    margin: 0px;
    color: white;
    line-height: 1.6;
}

p, a{
    font-family: 'Montserrat', sans-serif;
}

.intro-container{
    background-image: url('./assets/intro.jpg');
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    height: 60vh;
    display: grid;
}

.info{
    display: grid;
    grid-template-rows: repeat(3, 33%);
    margin: 15px;
    align-items: center;
    justify-items: center;
}

#title-intro{
    margin: 15px;
    grid-row: 1/2;
}

#content-intro{
    grid-row: 2/3;
    margin: 15px;
}

.bttn-purple{
    grid-row: 3/4;
    margin: 15px;
    height: 35px;
    width: 70%;
    font-size: 18px;
    font-weight: bold;
    background-color: #743762;
    border-color: #743762;
}

.about-container{
    display: grid;
    height: auto;
    grid-template-rows: repeat(2, auto);
    background-image: url('./assets/intro.jpg');
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
}

.about-me{
    grid-row: 1/2;
    background-color: #00072C73;
    margin: 15px;
    padding: 10px; 
    height: 70vh; 
    display: grid;
    grid-template-rows: repeat(3, auto);
    justify-items: center;
    align-items: center;
}

#title{
    grid-row: 1/2;
}

#content{
    grid-row: 2/3;
}

.contact-bttn{
    background-color: #743762;
    border-color: #743762;
    width: 60%;
    grid-row: 3/4;
    height: 35px;
    font-size: 18px;
    font-weight: bold;
    letter-spacing: .08em;
    cursor: pointer;
}

#contact-a{
    text-decoration: none;
    text-align: center;
}

.photo{
    grid-row: 2/3;
    margin: 20px;
    display: grid;
    align-items: center;
}

.karen{
    width: 100%;
}

.work{
    margin: 0;
    display: grid;
    grid-template-rows: repeat(2, auto);
    background-image: url('./assets/intro.jpg');
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
}

.intro-work{
    grid-row: 1/2;
    text-align: center;
    height: 25vh;
    margin: 10% 5% 0 5%;
}

.work-container{
    grid-row: 2/3;
    margin: 0;
    display: grid;
    grid-template-rows: repeat(3, auto);
}

.card{
    background-color: #74376273;
    height: 80vh;
    margin: 20px;
    display: grid;
    grid-template-rows: repeat(4, auto);
    justify-items: center;
    align-items:center;
}

.mockup{
    grid-row: 1/2;
    display: grid; 
}

.mockup > img{
    width: 70%;
    justify-self: center;
    margin-top: 20px;
}

.card-content{
    grid-row: 2/3;
    margin: 20px;
}

.card-link{
    grid-row: 3/4;
    width: 80%;
    display: grid;
    grid-template-columns: repeat(2, 50%);
    justify-items: center;
    font-weight: bold;
}

.link{
    grid-row: 3/4;
    font-size: 18px;
    font-weight: bold; 
    margin: 5px;
}

.tools{
    grid-row: 4/5;
    width: 90%;
    margin-bottom: 10px;
    text-align: center;
}

.contact-container{
    display: grid;
    background-image: url('./assets/intro.jpg');
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    height: 50vh;
    align-items: center;
    justify-items: center;
}

.contact{
    margin: 20px;
}

#social-media{
    width: 30px;
}

@media (min-width: 768px) {
    .about-container{
        height: auto;
        grid-template-rows: 40% 60%;
        justify-items: center;
    }

    .about-me{
        grid-row: 1/2;
        background-color: #00072C73;
        margin: 40px;
        padding: 10px; 
        height: 50vh; 
        display: grid;
        grid-template-rows: repeat(3, auto);
        justify-items: center;
        align-items: center;
    }

    .work-container{
        display: grid;
        grid-template-columns: repeat(3, auto);
        grid-template-rows: auto;
    }

    #content{
        grid-row: 2/3;
        padding: 10%;
    }

    .intro-work{
        font-size: 20px;
    }

  }

  @media (min-width: 1200px){
    .about-container{
        display: grid;
        grid-template-columns: 65% 35%;
        grid-template-rows: 1fr;
        height: auto;
    }  

    .about-me{
        height: 90vh;
        grid-row: 1/2;
        grid-column: 1/2;
        margin: 40px;
        display: grid;
        grid-template-rows: repeat(3, auto);
        grid-template-columns: repeat(4, 25%); 
        line-height: 1;
        font-size: larger;
        align-self: center;
    }

    #title{
        grid-row: 1/2;
        grid-column: 1/4;
    }
    
    #content{
        grid-row: 2/3;
        grid-column: 1/4;
        padding-right: 20%;
    }
    
    .contact-bttn{
        grid-row: 3/4;
        grid-column: 3/5;
    }

    .photo{
        grid-row: 1/2;
        grid-column: 2/3;
        margin: 30px;
        display: grid;
        align-items: center;
    }
    
    .karen{
        width: 100%;
        height: auto;
    }

  }